import React, {Component} from 'react';
import FeatureDisplayCard from './FeatureDisplayCard';
import ButtonCircle from './ButtonCircle';

class SideDisplay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            posts: [],
            postsPulled: 0
        }
    }

    async componentDidMount() {
        await fetch('https://jsonplaceholder.typicode.com/posts')
            .then(response => response.json())
            .then(json => {
                this.setState({
                    posts: json,
                    postsPulled: 1
                });
            });
    }

    render() {
        return (
            <div className="side-display">
                <FeatureDisplayCard className="padding-default" postsPulled={this.state.postsPulled}>
                    <i className="fas fa-globe-europe fa-3x margin-default-half-bottom"></i>
                    <h4>Nearly 200 countries worldwide, because freelance has no limits!</h4>
                </FeatureDisplayCard>

                <FeatureDisplayCard className="padding-default" postsPulled={this.state.postsPulled}>
                    <i className="fab fa-internet-explorer fa-3x margin-default-half-bottom"></i>
                    <h4>Support all platforms!</h4>
                </FeatureDisplayCard>

                <FeatureDisplayCard className="padding-default" postsPulled={this.state.postsPulled}>
                    <img src="./assets/icons/brain.svg" alt="brain.svg" className="margin-default-half-bottom" style={{filter: 'invert(28%) sepia(14%) saturate(0%) hue-rotate(185deg) brightness(100%) contrast(89%)'}}/>
                    <h4>Don't think, hire a professional!</h4>
                </FeatureDisplayCard>

                {/*<FeatureDisplayCard className="padding-default" postsPulled={this.state.postsPulled}>*/}
                    {/*<h2 className="margin-default-half-bottom">{this.state.posts.length > 0 ? this.state.posts[2].title : ''}</h2>*/}
                    {/*<p>{this.state.posts.length > 0 ? this.state.posts[2].body : ''}</p>*/}
                {/*</FeatureDisplayCard>*/}

                <div className="slide-buttons">
                    <ButtonCircle material="keyboard_arrow_up" className="prev"/>
                    <ButtonCircle material="keyboard_arrow_down" className="next"/>
                </div>
            </div>
        );
    };
}

export default SideDisplay;